import React, { useState } from 'react'

const App = () => {
  const [state, setstate] = useState("algo")
  fetch("https://curso-backend-app.herokuapp.com/api/usuarios").then((resp)=>{
    resp.json().then((data)=>{
      console.log(data);
      let {mensaje}=data;
      setstate(mensaje)
    })
  })
  return (
    <div>
      {state}
    </div>
  )
}

export default App
